@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.mkdf-comment-holder {
    @include mkdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .mkdf-comment-holder-inner {
        padding: 33px 0 20px;
    }
    
    .mkdf-comments-title {
        margin-bottom: 70px;
    }

    .mkdf-comment {

        &.mkdf-no-avatar {

            .mkdf-comment-text {
                padding: 0;
            }
        }
    }

    .mkdf-comment-list {
        position: relative;
        padding: 0 43px;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include mkdfRelativeHolderLayout();
            margin: 41px 0 0;
            padding: 29px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 110px;
        }
    }

    .mkdf-comment-image {
        position: relative;
        top: 0;
        left: 0;
        width: 120px;
        height: 120px;
        display: block;
        float: left;

        img {
            display: block;
        }
    }
    
    label {
        @include mkdfTagLineStyle(small);
    }

    .mkdf-comment-text {
        padding: 0 0 0 150px;
        min-height: 120px;

        .mkdf-comment-info {
            @include mkdfRelativeHolderLayout();
            margin: 6px 0 7px;
        }

        > a:not(:last-child) {
            margin-right: 30px;
        }
        
        .replay,
        .comment-reply-link,
        .comment-edit-link,
        #cancel-comment-reply-link {
            position: relative;
            display: inline-flex;
            align-items: center;
            justify-content: flex-start;
	        margin-top: 16px;
            font-family: $default-heading-font;
            font-size: 14px;
            font-weight: 700;
            letter-spacing: .1em;
            text-transform: uppercase;
            color: $default-heading-color;
            overflow: hidden;
            
            .mkdf-link-label {
                padding: 0 88px 0 0;
                transform: translateX(0);
                transition: all .3s ease-out;
            }
    
            &:before,
            &:after {
                content: '';
                position: absolute;
                height: 1px;
                vertical-align: middle;
                background-color: $default-heading-color;
                transition: all .3s ease-out;
            }
    
            &:before {
                width: 54px;
                transform: scale(0);
                transform-origin: left;
                opacity: .4;
            }
    
            &:after {
                width: 74px;
                height: 1px;
                right: 3px;
                transform: scale(1);
                transform-origin: right;
                opacity: 1;
            }
            
            .mkdf-link-arrow {
                top: 50%;
                right: 0;
                line-height: 1;
            }
    
            &:hover {
        
                &:before{
                    transform: scale(1);
                    opacity: 1;
                }
        
                &:after {
                    transform: scale(0);
                    opacity: .4;
                }
        
                .mkdf-link-label {
                    transform: translateX(72px);
                }
            }
        }
        
        .mkdf-comment-name {
            float: left;
            margin: 0;
        }

        .mkdf-comment-date {
            @include mkdfTagLineStyle(small);
            float: left;
            margin: 0;
            color: $first-main-color;
            line-height: 1;
        }

        .mkdf-text-holder p {
            margin: 7px 0;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 14px;
	            letter-spacing: .1em;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 30px;
            
            @include phone-portrait {
                margin-left: 0;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.mkdf-comment-form {
    @include mkdfRelativeHolderLayout();
    margin: 90px 0 40px;
    clear: both;

    .comment-reply-title {
        margin: 0 0 57px;
    }
    
    label {
        @include mkdfTagLineStyle(small);
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
    
    #wp-comment-cookies-consent {
        position: relative;
        top: 2px;
	    
	    .mkdf-safari & {
		    top: 0;
	    }
    }
    
    .form-submit {
        padding-top: 13px;
    }
}

@include ipad-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .mkdf-comment-holder {

        .mkdf-comment-list {
	        padding: 0;

            .children {
                padding: 0;
            }
        }

        .mkdf-comment-text {

            .mkdf-comment-date,
            .mkdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .mkdf-comment-holder {

        .mkdf-comment-image {
            float: none;
        }

        .mkdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.mkdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

#respond .comment-form-cookies-consent {
	margin: 15px 0 5px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include mkdfButtonDefaultStyle();
    @include mkdfButtonSize();
    @include mkdfButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include mkdfButtonSolidHoverColor();
    }
}

#respond input[type="text"]:last-of-type:not(:last-child) {
    margin-bottom: 0;
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.mkdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
}

.mkdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.mkdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.mkdf-slider-is-moving {
        .owl-item {
            .mkdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.mkdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                transform: translateY($pos);
            }
        }
    }

    &.mkdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $default-background-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;
            font-family: $default-heading-font;
            font-size: 14px;
            font-weight: 700;
            letter-spacing: .1em;
            text-transform: uppercase;
            line-height: 60px;
            color: #fff;
            transform: translateY(-50%);
	        overflow: hidden;
    
            .mkdf-button-mark {
	            
                &:before {
                    font-size: 13px;
                }
	            
                &:after {
                    content: '';
                    position: absolute;
                    display: block;
                    width: 74px;
                    height: 1px;
                    top: calc(50% - 1px);
                    background-color: currentColor;
                    transform: scaleX(1);
                    transition: .3s ease-in-out;
	                
	                @include ipad-landscape {
		                display: none;
	                }
                }
            }
    
            .mkdf-nav-label {
                position: relative;
                display: inline-block;
                transition: .3s ease-in-out;
                transform: translateX(0);
    
                &:before {
                    content: '';
                    position: absolute;
                    display: block;
                    width: 74px;
                    height: 1px;
                    top: calc(50% - 1px);
                    background-color: currentColor;
                    transition: .3s ease-in-out;
                    opacity: .4;
	                
	                @include ipad-landscape {
		                display: none;
	                }
                }
            }
            
            &:hover {
	            
                .mkdf-nav-label {
                    transition-delay: 0s;
                
                    &:before {
                        transform: translateX(0) scaleX(1);
                        opacity: 1;
                    }
                }
    
                .mkdf-button-mark {
                    &:after {
                        transform: scaleX(0);
                        opacity: .4;
                    }
                }
            }
        }
	
	    .owl-prev {
		    left: 35px;
        
            .mkdf-button-mark {
                margin: 0 93px 0 0;
	
	            @include ipad-landscape {
		            margin: 0;
	            }
    
                &:before {
                    transform: scaleX(-1);
                }
                
                &:after {
                    left: 8px;
                    transform-origin: left;
                }
            }
        
            .mkdf-nav-label {
	            
	            @include ipad-landscape {
		            margin: 0 0 0 5px;
	            }
	            
                &:before {
                    left: calc(100% + 20px);
                    transform: translateX(100%) scaleX(0);
                    transform-origin: left;
                }
            }
            
		    &:hover {
			    
                .mkdf-nav-label {
                    transform: translateX(-74px);
	
	                @include ipad-landscape {
		                transform: none;
	                }
                }
		    }
	    }
	
	    .owl-next {
		    right: 35px;
        
            .mkdf-button-mark {
                margin: 0 0 0 93px;
                right: 0;
	
	            @include ipad-landscape {
		            margin: 0;
	            }
            
                &:after {
                    right: 8px;
                    transform-origin: right;
                }
            }
        
            .mkdf-nav-label {
	
	            @include ipad-landscape {
		            margin: 0 5px 0 0;
	            }
	            
                &:before {
                    right: calc(100% + 20px);
                    transform: translateX(-100%) scaleX(0);
                    transform-origin: right;
                }
            }
            
            &:hover {
	            
                .mkdf-nav-label {
                    transform: translateX(74px);
	
	                @include ipad-landscape {
		                transform: none;
	                }
                }
            }
	    }
        
        @include phone-portrait{
            .owl-prev,
            .owl-next {
                .mkdf-nav-label {
                    display: none;
                }
            }
    
            .owl-prev {
                left: 15px;
            }
    
            .owl-next {
                right: 15px;
            }
        }
    }

    .owl-dots {
        position: relative;
	    display: flex;
	    align-items: center;
	    justify-content: flex-start;
        margin: 69px auto 0;
	    width: 49.65%;
        counter-reset: counter;
        
        &:before {
            content: '';
            position: absolute;
            display: block;
            width: 100%;
            height: 1px;
            top: 14px;
            left: 0;
            background-color: #d9d9d9;
        }
        
        &:after {
            content: '/';
            position: absolute;
            display: block;
            top: -8px;
            left: 20px;
	        font-family: $default-heading-font;
	        font-size: 14px;
	        line-height: 1;
	        font-weight: 700;
	        letter-spacing: .1em;
        }

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
	        margin: 8px 0 0 0;
	        width: 100%;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
	        transition: all .3s ease-in-out;
	        
	        &:before,
	        &:after {
		        position: absolute;
		        top: -10px;
		        font-family: $default-heading-font;
		        font-size: 14px;
		        font-weight: 700;
		        letter-spacing: .1em;
		        content: counter(counter,decimal-leading-zero);
		
		        .mkdf-ms-explorer & {
			        top: -14px;
		        }
	        }

	        &:before {
		        left: 30px;
		        counter-increment: counter;
		        visibility: hidden;
		        color: #7f7d80;
	        }
	
	        &:last-child {
		
		        &:before {
			        visibility: visible;
		        }
	        }
	        
	        &:after {
		        left: 0;
		        display: none;
	        }
	        
            span {
	            position: relative;
	            display: inline-block;
	            vertical-align: middle;
	            width: 100%;
	            height: 3px;
	            top: -2px;
	            left: 0;
	            border: 0;
	            border-radius: 0;
	            background-color: $first-main-color;
                box-sizing: border-box;
                /*animation: .3s mkdf-animate-pag-line-disappear forwards ease-out;*/
                transform: scaleX(0);
                transform-origin: left;
                transition: .3s ease-out;
	            
	            .mkdf-ms-explorer & {
		            top: -7px;
	            }
            }
	
	        &.active {
		        
		        &:after {
			        display: block;
		        }
	        }

            &:hover,
            &.active {

                span {
                    /*animation: .3s mkdf-animate-pag-line-appear ease-in;*/
                    transform: scaleX(1);
                }
            }
        }
    }
    
    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .mkdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .mkdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                transition: border-color 0.2s ease-out;
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.mkdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .mkdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

.mkdf-custom-pagination-holder {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin: 69px auto 0;
    width: 49.65%;
    
    .mkdf-light-skin &{
        .mkdf-custom-pagination-container.owl-dots .owl-dot:after{
            color: #fff;
        }
    }
    
    .mkdf-custom-pagination-container {
        display: flex;
        width: 100%;
        
        &.owl-dots {
            counter-reset: counter;
            
            &:before {
                content: '';
                position: absolute;
                display: block;
                width: 100%;
                height: 1px;
                top: 14px;
                left: 0;
                background-color: #d9d9d9;
            }
            
            &:after {
                content: '/';
                position: absolute;
                display: block;
                top: -8px;
                left: 20px;
                font-family: $default-heading-font;
                font-size: 14px;
                line-height: 1;
                font-weight: 700;
                letter-spacing: .1em;
            }
            
            .owl-dot {
                z-index: 9;
                display: inline-block;
                vertical-align: middle;
                margin: -5px 0 0 0;
                padding: 20px 0;
                width: 100%;
                background: none;
                border: 0;
                border-radius: 0;
                outline: none;
                -webkit-appearance: none;
                cursor: pointer;
                
                
                &:before,
                &:after {
                    position: absolute;
                    top: -10px;
                    font-family: $default-heading-font;
                    font-size: 14px;
                    font-weight: 700;
                    letter-spacing: .1em;
                    content: counter(counter,decimal-leading-zero);
                    
                    .mkdf-ms-explorer & {
                        top: -14px;
                    }
                }
                
                &:before {
                    left: 30px;
                    counter-increment: counter;
                    visibility: hidden;
                    color: #7f7d80;
                }
                
                &:last-child {
                    
                    &:before {
                        visibility: visible;
                    }
                }
                
                &:after {
                    left: 0;
                    opacity: 0;
                    color: #000;
                }
    
                span {
                    position: absolute;
                    display: none;
                }
                
                &.active {
                    
                    &:after {
                        opacity: 1;
                    }
                }
            }
        }
    }
    
    .mkdf-pagination-line-holder {
        position: absolute;
        width: 100%;
        height: 3px;
        top: 13px;
        pointer-events: none;
        
        .mkdf-pagination-line{
            background-color: red;
            position: relative;
            top: -16px;
            left: 0;
            display: inline-block;
            height: 100%;
            width: 150px;
            transition: left .3s;
            pointer-events: none;
        }
    }
}
/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

