    }

    .mkdf-page-not-found {
	    position: relative;
        display: flex;
	    align-items: center;
	    justify-content: flex-start;
	    width: 69.575%;
	    height: 100%;
	    margin: 0 auto;

        .mkdf-404-tagline {
            @include mkdfTagLineStyle('normal');
        }
	
	    .mkdf-pnf-content-holder {
		    width: 27%;
		    padding-right: 11.7%;
	    }
	
	   .mkdf-404-title-image {
		    width: 61.3%;
		   
		   img {
			   display: block;
		   }
	    }
	
	    .mkdf-404-title {
            margin: 16px 0 0;
        }

        .mkdf-404-text {
            margin: 19px 0 0;
        }

        .mkdf-btn {
            margin: 52px 0 0;

            &.mkdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
