	    .mkdf-page-not-found {
		    width: 80%;
		    padding: 20.7% 0 19.3%;
		
		    .mkdf-pnf-content-holder {
			    width: 40%;
		    }
		
		    .mkdf-404-title-image {
			    width: 48.3%;
		    }
	    }
    }

    @include ipad-portrait {
        .mkdf-page-not-found {
	        padding: 22.9% 0;
	        flex-direction: column;
	
	        .mkdf-pnf-content-holder {
		        padding: 0;
		        width: 100%;
	        }
	
	        .mkdf-404-title-image {
		        margin-top: 60px;
		        width: 100%;
	        }
        }
    }

    @include phone-landscape {
        .mkdf-page-not-found {
	        padding: 8% 0;
        }
    }
}

