/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#mkdf-back-to-top {
    position: fixed;
    width: 59px;
    right: 44px;
    bottom: 45px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
    visibility: hidden;

    &.off {
        opacity: 0;
        visibility: hidden;

        transition: opacity 0.15s ease, visibility .0s .15s;
    }

    &.on {
        opacity: 1;
        visibility: visible;

        transition: opacity 0.3s ease;
    }

    > span {
        position: relative;
	    display: flex;
	    align-items: flex-start;
	    justify-content: center;
        width: 59px;
        height: 59px;
        line-height: 59px;
        font-size: 12px;
        text-align: center;
        color: #fff;
        background-color: $first-main-color;
        border: 1px solid $first-main-color;
        border-radius: $default-border-radius;
        box-sizing: border-box;
        overflow: hidden;

        transition: height .2s ease;
        transition-delay: .05s;
        
        i {
            &.ion-ios-play {
                display: inline-block;
                transform: rotate(-90deg);
            }
        }

        &:after {
            content: '';
            position: relative;
	        display: inline-block;
	        width: 1px;
	        height: 38px;
	        top: calc(50% - 14px);
	        left: -3px;
	        z-index: 1;
	        background-color: #fff;
            transform-origin: top;
            transform: scaleY(0);
            transition: transform .15s ease-out;
            transition-delay: 0s;
        }

        &:hover {
	        height: 93px;
	        transition-delay: 0s;
	        
            &:after {
                transform: scaleY(1);
                transition: transform 0.3s ease-in-out;
                transition-delay: .05s;
            }
        }
    }

    span span {
        display: block;
        line-height: inherit;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.mkdf-self-hosted-video-holder {
    .mkdf-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
	
	.mejs-overlay:hover>.mejs-overlay-button {
		background-position: 0;
	}
    
    .mejs-overlay-button {
        background-image: url('../img/video-button.svg');
	    background-position: 0;
	    margin-top: -39px;
	    display: none;
		
	    &:before {
		    content: 'play';
		    position: absolute;
		    display: inline-block;
		    top: calc(50% - 19px);
		    left: auto;
		    padding-right: 78px;
		    font-family: $default-heading-font;
		    font-weight: 700;
		    font-size: 14px;
		    line-height: 1.5em;
		    letter-spacing: .1em;
		    text-transform: uppercase;
		    transform: translate((calc(-100% + 10px)), -50%);
		    color: #fff;
	    }
	    
	    &:after {
		    content: '';
		    position: absolute;
		    display: block;
		    width: 74px;
		    height: 1px;
		    background-color: #fff;
		    top: calc(50% - 20px);
		    left: calc(50% - 95px);
		    transition: transform .3s ease-in-out;
	    }
    }
	
	.mejs-overlay {
        
        &:hover {
    
            > .mejs-overlay-button {
    
                &:after {
	                transform: translateX(16px);
                }
            }
        }
	}
}

